*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #d2dbdd;
    font-family:"raleway", sans-serif;;
}
h1
{
    text-transform: uppercase;
    text-align: center;
    background:rgb(238, 122, 190);
    color: rgb(248, 244, 244);
    font-weight: bold;
    font-size: 1.5rem;
    padding: 20px;
    margin-bottom: 0px;
}
.cards
{
    height: 100%;
    width: 100%;
}
.card
{
    margin: 1.5%;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
    background-color: #fff;
    width: 21.25%;
    border-radius: 12px;
    box-shadow: 0px 13px 10px -7px rgba(0,0,0,0,0.1);
    float:left;
}
.card:hover
{
    box-shadow: 0px 30px 18px -8px rgba(0,0,0,0,0.1);
    transform: scale(1.05,1.05);
}
.card__img
{
    width:100%;
    height:235px;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;

}
.card__info
{
    background-color:#fff;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    padding:16px 24px 24px 24px;
    background-color: #fff;
}
.card__category
{
    font-family: "raleway", sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: bold;
    color: #868686;
    background-color: #fff; 
}
.card__title
{
    color: #868686;
    background-color: #fff;
    margin-bottom: 10px;
    margin-top: 5px;
    text-transform: capitalize;

}
button
{
  padding: 5px 8px;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
}
footer{
    position: relative;;
    font-size: 1.1rem;
    padding: 10px;
    display: inline-block;
    bottom: -500px;
    right:-20px;
    color:#515250;
}